import mongoose, { Schema, Document } from 'mongoose';

export interface IFolio extends Document {
    SYMBOL: string;
    PDATE: Date;
    PRICE: mongoose.Types.Decimal128;
    QNTY: number;
    AMT: mongoose.Types.Decimal128;
    BROKER: string;
    BROKERAGE: mongoose.Types.Decimal128;
    NAMT: mongoose.Types.Decimal128;
    FOLIO: string;
    TYPE: string;
    CPRICE: mongoose.Types.Decimal128;
    AGE: mongoose.Types.Decimal128;
    PL: mongoose.Types.Decimal128;
    username: string;
}

const folioSchema: Schema = new Schema({
    SYMBOL: { type: String, required: true },
    PDATE: { type: Date, required: true },
    PRICE: { type: mongoose.Types.Decimal128, required: true },
    QNTY: { type: Number, required: true },
    AMT: { type: mongoose.Types.Decimal128, required: true },
    BROKER: { type: String, required: true },
    BROKERAGE: { type: mongoose.Types.Decimal128, required: true },
    NAMT: { type: mongoose.Types.Decimal128, required: true },
    FOLIO: { type: String, required: true },
    TYPE: { type: String, required: true },
    CPRICE: { type: mongoose.Types.Decimal128, required: false },
    AGE: { type: mongoose.Types.Decimal128, required: false },
    PL: { type: mongoose.Types.Decimal128, required: false },
    username: { type: String, required: true }
}, {timestamps: true});

export const FolioModel = mongoose.model<IFolio>('Folio', folioSchema);
